header {
    padding: 1em;
    background: rgba(255, 255, 255, 0.779);
    box-shadow: rgba(124, 178, 185, 0.102) 0px 54px 55px, rgba(124, 178, 185, 0.02) 0px -12px 30px, rgba(124, 178, 185, 0.02) 0px 4px 6px, rgba(124, 178, 185, 0.07) 0px 12px 13px, rgba(124, 178, 185, 0.01) 0px -3px 5px;
    display: flex;
    place-content: space-between;
    position: sticky;
    .tomodachiLogo {
        position: relative;
        display: block;
        margin-top: .5em;
        margin-left: .5em;
        width: 6em;
    }

    ul {
    display: flex;
    text-decoration: none;
    list-style-type: none;

        li {
            font-size: 1.1em;
            margin-bottom: 1em;
            margin-left: 2em;
            position: relative;
        }
    }

    nav {
        display: none;
    }

    .mobile-menu {
        cursor: pointer;
    }
}


@media only screen and (max-device-width: 815px) {

    header {
        .tomodachiLogo {
            width: 20em;
        }
  
        .mobile-menu {
            width: 7em;
        }
    }
}



@media only screen and (min-width: 815px) and (min-device-width: 815px) {
    .mobile-menu, .mobile-menu-exit {
        display: none;
    }
    header {
        display: flex;
        justify-content: space-around;
    }
    header nav {
        display: flex;
        background: none;
        position: unset;
        height: auto;
        width: 100%;
  
        ul {
            color: black;
            top: 0.7em;
            font-size: 0.9em;
            margin-bottom: 1em;
            margin-left: 2em;
            position: relative;
  
            a {
            color: black;
            text-decoration: none;
            }
        }
  
        a:hover {
            font-weight: bold;
        }
  
        .current {
            font-weight: bold;
            color: #90BFD7;
        }
  
        .log-in {
            padding: 9px 10px;
            border-radius: 21px;
            box-shadow: rgb(144, 191, 215) 0 0px 15px 0;
        }
  
        .log-in:hover {
            font-weight: bolder;
        }
    }
  
}

